
import { defineComponent } from "vue";

export default defineComponent({
  name: "McvValidationErrors",
  props: {
    errors: {
      type: Object,
      required: true,
    },
  },
  computed: {
    errorMessages(): string[] {
      return Object.keys(this.errors).map(key => {
        const messages = this.errors[key].join(", ");
        return `${key} ${messages}`;
      });
    },
  },
});
