
import { PropType, defineComponent } from "vue";
import { ArticleFormData } from "@/models/article";
import McvValidationErrors from "@/components/ValidationErrors.vue";

export default defineComponent({
  name: "McvArticleForm",
  data() {
    return {
      title: this.initialData.title,
      description: this.initialData.description,
      body: this.initialData.body,
      tags: this.initialData.tagList.join(", "),
    };
  },
  props: {
    initialData: {
      type: Object as PropType<ArticleFormData>,
      required: true,
    },
    isSubmitting: {
      type: Boolean,
      required: true,
    },
    errors: {
      type: Object,
      required: false,
    },
  },
  emits: ["submit"],
  methods: {
    submit(): void {
      const formValue = {
        title: this.title,
        description: this.description,
        body: this.body,
        tagList: this.tags.split(",").map(tag => tag.trim()),
      };
      this.$emit("submit", formValue);
    },
  },
  components: {
    McvValidationErrors,
  },
});
