
import { defineComponent } from "vue";
import { ArticleAction, ArticleGetter } from "@/store/modules/article";
import { AuthGetter } from "@/store/modules/auth";
import { Article, User } from "@/models";
import McvLoading from "@/components/Loading.vue";

export default defineComponent({
  name: "McvArticle",
  mounted(): void {
    const slug = this.$route.params["slug"] as string;
    this.$store.dispatch(ArticleAction.GetArticle, { slug });
  },
  computed: {
    article(): Article | null {
      return this.$store.getters[ArticleGetter.Article];
    },
    isLoading(): boolean {
      return this.$store.getters[ArticleGetter.Loading];
    },
    currentUser(): User | null {
      return this.$store.getters[AuthGetter.User];
    },
    isAuthor(): boolean {
      if (!this.currentUser || !this.article) {
        return false;
      }
      return this.currentUser.username === this.article.author.username;
    },
  },
  methods: {
    deleteArticle(): void {
      if (!this.isAuthor) {
        return;
      }

      const slug = this.$route.params["slug"] as string;
      this.$store
        .dispatch(ArticleAction.DeleteArticle, { slug })
        .then(() => this.$router.push({ name: "home" }));
    },
  },
  components: {
    McvLoading,
  },
});
